@use '@styles' as styles;
@use '@colors' as *;
@use '@spacing' as *;

.products {
  align-self: center;
  width: 1370px;
  @include styles.internal-container;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  justify-content: center;
  text-align: center;

  &__item {
  }

  @media (max-width: 1023px) {
    font-size: 26px;
  }
}

.blockContainer {
  background-color: #fafafa;
  padding-top: 100px;
  @include styles.block-container;
}

.p1 {
  align-self: center;
  max-width: 640px;
  text-align: center;
  margin-bottom: 60px;

  @media (max-width: 1023px) {
    font-size: 14px;
  }
}

.searchBar {
  display: flex;
  gap: $space-xl;
  margin-bottom: $space-xl;

  @media (max-width: 1023px) {
    font-size: 18px;
  }

  &__input {
    flex-grow: 1;
  }
}

.filter {
  align-self: flex-start;
}

.totalProducts {
  display: flex;
  align-self: flex-start;
  align-items: center;
  margin: 50px 0;

  &__txt {
    font-weight: 700;
    font-size: 44px;

    @media (max-width: 1023px) {
      font-size: 18px;
    }
  }

  &__count {
    padding: 5px 16px;
    background: #f9f9f9;
    border-radius: 20px;
    color: $text-accent;
    font-weight: 700;
  }
}

.productsGrid {
  width: 100%;
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
  gap: 60px;

  & a {
    text-decoration: none;
  }

  @media (max-width: 1023px) {
    gap: 15px;
  }
}
